"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { submitTicket, PROBLEM_CATEGORIES, SECTORS, type ProblemCategory, type Sector } from "@/lib/api"
import Link from "next/link"

export default function NewTicketPage() {
  const router = useRouter()
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    nome: "",
    email: "",
    problema: "" as ProblemCategory,
    setor: "" as Sector,
    descricao: "",
    prioridade: "media" as "baixa" | "media" | "alta" | "urgente",
    justificativaPrioridade: ""
  })
  const [errors, setErrors] = useState<Record<string, string>>({})
  const [success, setSuccess] = useState(false)

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.nome.trim()) {
      newErrors.nome = "Nome é obrigatório"
    }
    if (!formData.email.trim()) {
      newErrors.email = "Email é obrigatório"
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = "Email inválido"
    }
    if (!formData.problema) {
      newErrors.problema = "Problema é obrigatório"
    }
    if (!formData.setor) {
      newErrors.setor = "Setor é obrigatório"
    }
    if (!formData.descricao.trim()) {
      newErrors.descricao = "Descrição é obrigatória"
    }
    if ((formData.prioridade === "alta" || formData.prioridade === "urgente") && !formData.justificativaPrioridade?.trim()) {
      newErrors.justificativaPrioridade = "Justificativa é obrigatória para prioridade alta ou urgente"
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) {
      return
    }

    setLoading(true)
    try {
      await submitTicket(formData)
      setSuccess(true)
      setTimeout(() => {
        router.push("/")
      }, 2000)
    } catch (error) {
      console.error("Error submitting ticket:", error)
      setErrors({ submit: "Erro ao criar chamado. Tente novamente." })
    } finally {
      setLoading(false)
    }
  }

  if (success) {
    return (
      <div className="min-h-screen p-8 bg-background">
        <div className="max-w-2xl mx-auto text-center">
          <Card className="bg-card border-border">
            <CardContent className="pt-6">
              <div className="mb-4">
                <svg
                  className="mx-auto h-12 w-12 text-green-600"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M5 13l4 4L19 7"
                  />
                </svg>
              </div>
              <h2 className="text-2xl font-bold mb-2 text-foreground">Chamado Criado com Sucesso!</h2>
              <p className="text-muted-foreground mb-4">
                Seu chamado foi registrado e será atendido em breve.
              </p>
              <p className="text-muted-foreground text-sm mb-4">
                Redirecionando para a página inicial...
              </p>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen p-8 bg-background">
      <div className="max-w-2xl mx-auto">
        <Card className="bg-card border-border">
          <CardHeader>
            <div className="flex justify-between items-center">
              <CardTitle>Novo Chamado</CardTitle>
              <Link href="/">
                <Button variant="outline">Voltar</Button>
              </Link>
            </div>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="space-y-2">
                <label htmlFor="nome" className="text-sm font-medium text-foreground">
                  Nome
                </label>
                <Input
                  id="nome"
                  value={formData.nome}
                  onChange={(e) => setFormData({ ...formData, nome: e.target.value })}
                  className={errors.nome ? "border-red-500" : ""}
                  placeholder="Digite seu nome e sobrenome"
                />
                {errors.nome && (
                  <p className="text-red-500 text-sm">{errors.nome}</p>
                )}
              </div>

              <div className="space-y-2">
                <label htmlFor="email" className="text-sm font-medium text-foreground">
                  Email
                </label>
                <Input
                  id="email"
                  type="email"
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  className={errors.email ? "border-red-500" : ""}
                />
                {errors.email && (
                  <p className="text-red-500 text-sm">{errors.email}</p>
                )}
              </div>

              <div className="space-y-2">
                <label htmlFor="setor" className="text-sm font-medium text-foreground">
                  Setor
                </label>
                <Select
                  value={formData.setor}
                  onValueChange={(value: Sector) =>
                    setFormData({ ...formData, setor: value })
                  }
                >
                  <SelectTrigger className={errors.setor ? "border-red-500" : ""}>
                    <SelectValue placeholder="Selecione seu setor" />
                  </SelectTrigger>
                  <SelectContent>
                    {SECTORS.map((sector) => (
                      <SelectItem key={sector.value} value={sector.value}>
                        {sector.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                {errors.setor && (
                  <p className="text-red-500 text-sm">{errors.setor}</p>
                )}
              </div>

              <div className="space-y-2">
                <label htmlFor="problema" className="text-sm font-medium text-foreground">
                  Problema
                </label>
                <Select
                  value={formData.problema}
                  onValueChange={(value: ProblemCategory) =>
                    setFormData({ ...formData, problema: value })
                  }
                >
                  <SelectTrigger className={errors.problema ? "border-red-500" : ""}>
                    <SelectValue placeholder="Selecione o tipo de problema" />
                  </SelectTrigger>
                  <SelectContent>
                    {PROBLEM_CATEGORIES.map((category) => (
                      <SelectItem key={category.value} value={category.value}>
                        {category.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                {errors.problema && (
                  <p className="text-red-500 text-sm">{errors.problema}</p>
                )}
              </div>

              <div className="space-y-2">
                <label htmlFor="descricao" className="text-sm font-medium text-foreground">
                  Descrição
                </label>
                <Textarea
                  id="descricao"
                  value={formData.descricao}
                  onChange={(e) => setFormData({ ...formData, descricao: e.target.value })}
                  className={errors.descricao ? "border-red-500" : ""}
                  rows={4}
                />
                {errors.descricao && (
                  <p className="text-red-500 text-sm">{errors.descricao}</p>
                )}
              </div>

              <div className="space-y-4">
                <div className="space-y-2">
                  <label htmlFor="prioridade" className="text-sm font-medium text-foreground">
                    Prioridade
                  </label>
                  <Select
                    value={formData.prioridade}
                    onValueChange={(value: "baixa" | "media" | "alta" | "urgente") =>
                      setFormData({ 
                        ...formData, 
                        prioridade: value,
                        justificativaPrioridade: value === "baixa" || value === "media" ? "" : formData.justificativaPrioridade
                      })
                    }
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Selecione a prioridade" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="baixa">Baixa</SelectItem>
                      <SelectItem value="media">Média</SelectItem>
                      <SelectItem value="alta">Alta</SelectItem>
                      <SelectItem value="urgente">Urgente</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                {(formData.prioridade === "alta" || formData.prioridade === "urgente") && (
                  <div className="space-y-2">
                    <label htmlFor="justificativaPrioridade" className="text-sm font-medium text-foreground">
                      Justificativa da Prioridade
                    </label>
                    <Textarea
                      id="justificativaPrioridade"
                      value={formData.justificativaPrioridade}
                      onChange={(e) => setFormData({ ...formData, justificativaPrioridade: e.target.value })}
                      className={errors.justificativaPrioridade ? "border-red-500" : ""}
                      placeholder="Por favor, explique por que este chamado é de alta prioridade"
                      rows={3}
                    />
                    {errors.justificativaPrioridade && (
                      <p className="text-red-500 text-sm">{errors.justificativaPrioridade}</p>
                    )}
                  </div>
                )}
              </div>

              {errors.submit && (
                <p className="text-red-500 text-sm">{errors.submit}</p>
              )}

              <div className="flex justify-end">
                <Button type="submit" disabled={loading}>
                  {loading ? "Criando..." : "Criar Chamado"}
                </Button>
              </div>
            </form>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
